import { GCommon } from './../store/getters'
import { mapGetters } from 'vuex'

export default {
    computed: {
        ...mapGetters({
            currrencySign: GCommon.GET_CURRENCY_SIGN
        })
    },
    methods: {
        formatPrice(price) {
            return `${this.currrencySign} ${price}`
        },
        formatThousaund(num) {
            return num.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
        }
    },
}